.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

body {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: inherit;
  max-width: 1920px; }

a {
  color: inherit;
  text-decoration: none; }

ul {
  list-style: none; }

.page-header {
  border: 1px solid black;
  background-color: #1B2D37;
  color: #FFFFFF;
  padding-left: 253px;
  padding-right: 256px;
  padding-top: 35px;
  padding-bottom: 30px;
  display: -ms-flex;
  display: flex;
  justify-content: space-between; }

.page-header__logo {
  font-weight: bold;
  font-size: 25px;
  line-height: 29px;
  padding-left: 68px;
  position: relative; }
  .page-header__logo::before {
    content: "";
    width: 52px;
    height: 52px;
    position: absolute;
    left: 0;
    top: -5px;
    background-image: url("../img/smart-device-logo.svg"); }
  .page-header__logo span {
    font-family: "Roboto", "Arial", sans-serif;
    font-size: 12px;
    line-height: 14px;
    font-weight: 500;
    color: #788891;
    display: block; }

.page-header__contacts {
  display: -ms-flex;
  display: flex; }

.page-header__contacts-list {
  font-family: "PT Mono", serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 18px;
  display: -ms-flex;
  display: flex;
  justify-content: space-between; }

.page-header__contacts-item {
  padding-left: 40px;
  position: relative; }
  .page-header__contacts-item::before {
    content: "";
    position: absolute;
    width: 20px;
    height: 20px;
    background-repeat: no-repeat;
    background-position: center; }
  .page-header__contacts-item--mail {
    margin-left: 88px; }
    .page-header__contacts-item--mail::before {
      left: 0;
      background-image: url("../img/phone-call.svg"); }
  .page-header__contacts-item--phone::before {
    left: 0;
    background-image: url("../img/envelope.svg"); }

.page-header__button {
  background-color: inherit;
  border: 1px solid #FFFFFF;
  border-radius: 5px;
  width: 180px;
  height: 45px;
  color: inherit;
  text-transform: uppercase;
  font-family: inherit;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  cursor: pointer;
  margin-left: 45px; }
